import React from 'react';

const Dashboard = React.lazy(() => import('./views/Dashboard'));

const Clients = React.lazy(() => import('./views/Configurations/Clients'));
const ClientUsers = React.lazy(() => import('./views/Configurations/ClientUsers'));
const Assignments = React.lazy(() => import('./views/Configurations/Assignments'));


// https://github.com/ReactTraining/react-router/tree/master/packages/react-router-config
const routes = [
  { path: '/', exact: true, name: '' },
  { path: '/dashboard', name: 'Dashboard', component: Dashboard },

  { path: '/clients', exact: true, name: 'Clients List', component: Clients },
  { path: '/users', exact: true, name: 'Users List', component: ClientUsers },
  { path: '/assignments', exact: true, name: 'Assignments List', component: Assignments },
  
];

export default routes;
